import { Modal, ModalProps } from "./Modal"
import { SelectList } from "./SelectList"
import * as React from 'react';
import { debounce } from "../constants/utils";
import { searchCategories } from "../api/services/articles";
import api from "../api";
import { useSelector } from "react-redux";

export const OptionsModal = (props: ModalProps) => {
    const [searchResults, setSearchResults] = React.useState<any[]>([]);
    const search = debounce(async (term: string) => {
        const res = await searchCategories(term);
        setSearchResults(res);
    });
    const room = useSelector((state: any) => state.game.currentRoom);

    return (
        <Modal onShow={props.onShow} onHide={props.onHide} visible={props.visible}>
            <h1>
                Game Options 
            </h1>
            <p>Category:</p>
            <div>
                <input type="text" placeholder='category' onChange={(e) => {search(e.target.value)}} />
                <SelectList items={searchResults} visible={true} onSelect={async (term: any) => {
                    await api.patch(`/api/game/room/${room.uuid}`, {category: term});
                    props.onHide?.();
                    setSearchResults([]); // empty the results.
                }} />
            </div>
        </Modal>
    )
}