
import api from '..';
import socket from '../../constants/socket';
import { Room } from '../../models/game.model';
import { Player } from '../../models/player.model';

export const createGame = async (): Promise<string> => {
    const room = await api.post('/api/game/create');
    console.log(room);
    return room.data.uuid;
}

export const joinGame = async (uuid: string): Promise<Player | null> => {
    try {
        const player = await api.post(`/api/game/join/${uuid}`);
        // send a message via socket.io to establish a link between
        // this client socket and the player's UUID
        socket.emit('player-join', {roomUUID: uuid});
        return player.data;
    }
    catch (err) {
        console.log(err);
        return null;
    }
}

export const fetchGame = async (uuid: string): Promise<Room | null> => {
    try {
        const room = await api.get(`/api/game/room/${uuid}`);
        console.log(room);
        return room.data;
    }
    catch (err) {
        console.log(err);
        return null;
    }
}

export const changeUsername = async (player: Player, username: string): Promise<Player | null> => {
    try {
        const res = await api.patch(`/api/game/room/${player.roomUUID}/${player.uuid}`, {username});
        return res.data;
    }
    catch (err) {
        console.log(err);
        return null;
    }
}

export const leaveGame = async (player: Player): Promise<Player | null> => {
    try {
        const res = await api.delete(`/api/game/room/${player.roomUUID}/${player.uuid}`);
        return res.data;
    }
    catch (err) {
        console.log(err);
        return null;
    }
}