import { wikiAPI } from '..';
import { Article } from '../../models/article.model';

export const getArticle = async (pageTitle: string): Promise<Article> => {
    const result = await wikiAPI.get("", {action: 'parse', format: 'json', page: pageTitle, origin: '*'});
    return {title: result.data.parse.title, content: result.data.parse.text['*']};
}

export const getRandomArticle = async (): Promise<Article> => {
    // const articleTitleResponse = await api.get('/api/article/random');
    const articleTitleResponse = await wikiAPI.get("", {action: "query", format: 'json', list: 'random', rnnamespace: 0, origin: '*'});
    const articleTitle = articleTitleResponse.data.query.random[0].title;
    const result: Article = await getArticle(articleTitle);
    return result;
}

export const getCategories = async (title: string): Promise<string[]> => {
    const result = await wikiAPI.get("", {action: 'query', format: 'json', prop: 'categories', titles: title, origin: '*'});
    const categories = result.data.query.pages[Object.keys(result.data.query.pages)[0]].categories;
    return categories.map((category: any) => category.title.replace(/^Category:/, ''));
}

export const searchCategories = async (searchTerm: string): Promise<string[]> => {
    // get's top 10 categories from search.
    const result = await wikiAPI.get("", {action: 'query', format: 'json', list: 'search', srnamespace: 14, srsearch: searchTerm, origin: '*'});
    const categories = result.data.query.search.map((category: any) => category.title.replace(/^Category:/, ''));
    return categories;
}