import styled from 'styled-components';

const theme = {
    colors: {
        primaryColor: "#cccccc",
        secondaryColor: "#888888",
        tertiaryColor: "#333333",

        backgroundColor: "#eeeeee",

        primaryTextColor: "#111111",
        secondaryTextColor: "#333333",

        linkColor: "#1e71f7",
        highlightColor: "#fcda2b"
    },
    fonts: {
        primary: "Roboto Mono"
    },
    spacing: {

    }
}

export default theme;

export const lighter = (color: string) => {
    // compute the rgb values of the color
    let r: number = Math.min(Number("0x" + color.slice(1, 3)) + 60, 255);
    let g: number = Math.min(Number("0x" + color.slice(3, 5)) + 60, 255);
    let b: number = Math.min(Number("0x" + color.slice(5, 7)) + 60, 255);
    return `rgb(${r},${g},${b})`;
}

export const darker = (color: string) => {
    // compute the rgb values of the color
    let r: number = Math.max(Number("0x" + color.slice(1, 3)) - 60, 0);
    let g: number = Math.max(Number("0x" + color.slice(3, 5)) - 60, 0);
    let b: number = Math.max(Number("0x" + color.slice(5, 7)) - 60, 0);
    return `rgb(${r},${g},${b})`;
}

export const Link = styled.a`
    text-decoration: none;
    color: ${theme.colors.linkColor};
    transition: color 0.2s ease-in-out;
    cursor: pointer;

    &:hover {
        color: ${lighter(theme.colors.linkColor)};
    }
`

export const CenterHorizontal = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
`
