import styled from "styled-components";
import theme from "../constants/theme";
import { createGame } from "../api/services/game";
import { useNavigate } from "react-router-dom";
import { ActionButton } from "../components/ActionButton";

const MainPageTitle = styled.h1`
    text-align: center;
    font-family: ${theme.fonts.primary};
    font-size: 64px;
    margin-bottom: 15px;
`

const ActionArea = styled.div`
    text-align: center;
`

const HowToPlaySection = styled.div`
    font-family: ${theme.fonts.primary};
    font-size: 1.5em;
    max-width: 800px;
    padding-inline: 15px;
    margin: auto;
`

const HowToPlayStep = styled.li`
    font-size: 0.9em;
    margin-bottom: 8px;
`

const HowToPlayTitle = styled.h2`
    text-align: center;
    font-size: 1.6rem;
    margin: 15px;
`

function MainPage() {
    const navigate = useNavigate();

    return (
        <>
            <MainPageTitle>
                Wiki-lie
            </MainPageTitle>
            <ActionArea>
                <ActionButton onClick={() => {
                    createGame().then(res => {
                        const uuid = res;
                        console.log(`Created game with join UUID ${uuid}`);
                        console.log(`You can join the game at ${window.location.origin}/game/${uuid}`);
                        // navigating to the game page.
                        navigate(`/game/${uuid}`);
                    });
                }}>
                    Create Game
                </ActionButton>
            </ActionArea>

            <HowToPlaySection>
                <HowToPlayTitle>
                    How to Play:
                </HowToPlayTitle>
                <hr></hr>
                <ol>
                    <HowToPlayStep>
                        One player is designated as the guesser
                    </HowToPlayStep>
                    <HowToPlayStep>
                        All other players find an obscure Wikipedia article and read a bit about it.
                    </HowToPlayStep>
                    <HowToPlayStep>
                        An article will be chosen randomly and all players must convince the guesser that it's their article by describing what it's about.
                    </HowToPlayStep>
                    <HowToPlayStep>
                        If it's not your article, you have to make something up!
                    </HowToPlayStep>
                    <HowToPlayStep>
                    At least 3 players are required, but you can play with however many you want!

                    </HowToPlayStep>
                </ol>
            </HowToPlaySection>
        </>
    );
}

export { MainPage };